$size--block: 100px;
$margin-right: 1px;
$start:
  "mon" ($size--block) * 0,
  "tue" ($size--block * 1) + $margin-right,
  "wed" ($size--block * 2) + ($margin-right + 1),
  "thu" ($size--block * 3)+ ($margin-right + 2) ,
  "fri" ($size--block * 4) + ($margin-right + 3),
  "sat" ($size--block * 5) + ($margin-right + 4),
  "sun" ($size--block * 6) + ($margin-right + 5);
